import { stopSubmit } from 'redux-form';
import {authAPI, securityAPI} from './../api/api'

const SET_AUTH_UESER_DATA = 'it/auth/SET_AUTH_UESER_DATA';
const GET_CAPTCHA_URL_SUCCESS = 'it/auth/GET_CAPTCHA_URL_SUCCESS';

const initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false,
    captchaUrl: null, 
}

const authReducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_AUTH_UESER_DATA:
        case GET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                ...action.payload
            }

        default:
            return state;
    }
}

export const setAuthUserData = (id, email, login, isAuth) => ({type: SET_AUTH_UESER_DATA, payload:{id, email, login, isAuth}});
export const getCaptchaUrlSuccess = (captchaUrl) =>  ({ type: GET_CAPTCHA_URL_SUCCESS, payload: {captchaUrl}});

export const getHeaderThunk = () => async (dispatch) => {
    let response = await authAPI.getHeader()
    
    if (response.data.resultCode === 0) {
        let { id, email, login } = response.data.data;
        dispatch(setAuthUserData(id, email, login, true));
    }

}

export const loginThunk = (email, password, rememberMe = false, captcha) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe, captcha);

    if (response.data.resultCode === 0) {
        dispatch(getHeaderThunk());
    } else {
        if(response.data.resultCode === 10) {
            dispatch(getCaptchaUrl());
        }
        let message = response.data.messages.length > 0 ? response.data.messages[0] : "Some error"
        dispatch(stopSubmit('login', { _error: message }))
    }
}

export const logoutThunk = () => async (dispatch) => {
    let response = await authAPI.logout()

    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
    }
}

export const getCaptchaUrl = () => async (dispatch) => {
    const response = await securityAPI.getSecurityAPI();
    const captchaUrl = response.data.url;
    dispatch(getCaptchaUrlSuccess(captchaUrl));
}


export default authReducer;